.main-busca{
    display: flex;
    justify-content: flex-end;


    .wrapper-busca{
        position: relative;
        padding-top: 20px;
        display: flex;
        padding-right: 15px;

        >input{
            width: 400px;
            border-radius: 76px;
            padding-left: 10px;
            text-align: center;
        }

        >button{
            text-align: center;
            width: 100px;
            height: 30px;
            font-family: 'Viga',Arial, Helvetica, sans-serif;
            font-size: 1em;
            border-radius: 8px;
            color: white;
            background-color: #bc36f4;
        }
    }
}