.card-category:first-child{
    border-top: 1px solid #EDEDED;
}

.card-category{
    height: 250px;
    margin: 10px 0 10px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #EDEDED;

    .info-category{
        
        .title{
            padding-bottom: 10px;
        }
    }

    .icon-category{
        display: flex;
        height: 150px;
        padding-left: 30px;

        >img{
            clip-path: circle(40%);
        }

    }
    
}

