.main-topseller{
    text-align: center;
    height: 100%;


    .wrapper-maisvendidos{
        display: flex;
        justify-content: center;
        gap: 20px;
        padding-top: 15px;

        .locate-info{
            display: flex;
            align-items: center;
            
            >img{
                margin-right: 1em;
                width: 50px;
                height: 50px;
                max-width: 100%;
            }

            >p{
                font-family: 'Viga', Arial, Helvetica, sans-serif;
                color: white;
                margin-bottom: 0;
                text-shadow: 1px 1px 2px #000;
            }
        }

        .produto:nth-child(even){
            margin: 0px 20px 0 20px;
        }
    }
}
        